'use strict';

// MODULES //

import isAccessorArray from '@stdlib/array-base-assert-is-accessor-array';
import accessorSetter from '@stdlib/array-base-accessor-setter';
import setter from '@stdlib/array-base-setter';
import dtype from '@stdlib/array-dtype';


// MAIN //

/**
* Returns an accessor function for setting an element in an array-like object.
*
* @param {Collection} x - input array
* @returns {Function} accessor
*
* @example
* import resolveGetter from '@stdlib/array-base-resolve-getter';
*
* var arr = [ 1, 2, 3, 4 ];
*
* var set = resolveSetter( arr );
* // returns <Function>
*
* set( arr, 2, 10 );
*
* var get = resolveGetter( arr );
* // returns <Function>
*
* var v = get( arr, 2 );
* // returns 10
*/
function resolveSetter( x ) {
	var dt = dtype( x );
	if ( isAccessorArray( x ) ) {
		return accessorSetter( dt );
	}
	return setter( dt );
}


// EXPORTS //

export default resolveSetter;
